@use './../abstracts/mixins' as *;

.button {
  font-family: var(--unnamed-font-family-brandon-grotesque);
  font-style: var(--unnamed-font-style-normal);
  font-weight: var(--unnamed-font-weight-medium);
  font-size: 14px;
  letter-spacing: 1.4px;
  text-transform: var(--unnamed-text-transform-uppercase);
  color: var(--green-light);
  text-decoration: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: 0.2s color ease-in;

  @include md {
    font-size: 11px;
    letter-spacing: 1.1px;
  }

  &--mid-green {
    color: var(--mid-green);

    .button__arrow {
      border-color: var(--mid-green);
    }
  }

  &:hover,
  &:focus {
    color: var(--gold);

    .button__arrow {
      border-color: var(--gold);
      background: var(--gold);

      svg,
      path {
        stroke: var(--dark-green);
      }
    }
  }

  &__arrow {
    width: 33px;
    height: 33px;
    transition: 0.2s all ease-in;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--green-light);
    margin-right: 9px;
    flex-shrink: 0;

    @include md {
      width: 26px;
      margin-right: 7px;
      height: 26px;
    }

    svg {
      width: 12px;
      height: 17px;
      margin-left: 2px;

      @include md {
        transform: scale(0.7);
      }
    }
  }
}

.button-outlined {
  font-family: var(--unnamed-font-family-brandon-grotesque);
  background: var(--white);
  border: 2px solid var(--gold);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 27px;
  color: var(--mid-green);
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  transition: 0.2s all ease-in;
  cursor: pointer;
  height: 53px;
  width: 100%;
  text-decoration: none;
  font-weight: 500;
  position: relative;

  @include md {
    height: 44px;
  }

  &__arrow {
    width: 14px;
    height: 17px;
    position: absolute;
    top: 50%;
    left: 22px;
    top: 50%;
    transform: translateY(-50%);

    @include md {
      left: 18px;
    }
  }

  &:hover,
  &:focus {
    color: var(--white);
    background: var(--gold);

    .button-outlined__arrow path {
      stroke: var(--white);
    }
  }
}
