@use 'base/typography';
@use 'base/reset';

@use 'abstracts/variables';
@use 'abstracts/mixins' as *;

// components

@use 'components/button';
@use 'components/menu';
@use 'components/header';
@use 'components/footer';

.container {
  padding: 0 30px;
  width: 100%;
  max-width: 1190px;
  margin: 0 auto;

  @include md {
    padding: 0 23px;
  }
}

body {
  background: var(--dark-green);
  font-family: var(--unnamed-font-family-brandon-grotesque);
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

.section-title {
  font-size: 28px;
  line-height: 39px;
  letter-spacing: 2.8px;
  color: var(--mid-green);
  padding-left: 3px;
  text-transform: uppercase;

  @include md {
    font-size: 20px;
    line-height: 29px;
    letter-spacing: 2px;
    padding-left: 0;
  }

  &::before {
    content: '/';
    font-size: 28px;
    color: var(--gold);
    display: inline-block;
    margin-right: 9px;

    @include md {
      margin-right: 6px;
      font-size: 20px;
      letter-spacing: 2px;
    }
  }
}
