@use './../abstracts/mixins' as *;

.header {
  padding-left: 43px;
  padding-top: 30px;
  display: flex;
  justify-content: space-between;

  @include md {
    padding-left: 23px;
    padding-top: 27px;
  }

  &__title {
    margin-top: 16px;

    @include md {
      margin-top: 0;
    }

    img {
      @include md {
        width: 205px;
      }
    }
  }

  &__hamburger {
    width: 45px;
    height: 45px;
    background: var(--dark-green);
    position: fixed;
    top: 30px;
    right: 48px;
    border: 0;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.2s all ease-in;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 9;

    @include md {
      width: 34px;
      height: 34px;
      top: 16px;
      right: 8px;
    }

    &:hover,
    &:focus {
      background-color: var(--gold);

      span {
        background-color: var(--dark-green);
      }
    }

    span {
      width: 26px;
      height: 2px;
      border-radius: 20px;
      background: var(--green-light);
      display: block;
      margin: 5px 0;

      @include md {
        width: 19px;
        margin: 4px 0;
      }
    }
  }
}
