@use './../abstracts/mixins' as *;

.footer {
  background: var(--dark-green);
  padding: 75px 0 62px 0;

  @include md {
    padding-bottom: 39px;
    padding-top: 52px;
  }

  &__container {
    display: flex;

    @include md {
      display: block;
    }
  }

  &__info,
  &__nav {
    width: 50%;

    @include md {
      width: 100%;
    }
  }

  &__info {
    &::after {
      margin-top: 42px;
      margin-bottom: 21px;
      content: '';
      height: 39px;
      background: url('./../assets/footer-dots.webp');
      width: 388px;
      display: block;

      @include md {
        width: 100%;
        max-width: 388px;
        overflow: hidden;
        margin-bottom: 43px;
        margin-bottom: 20px;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &__logo {
      display: block;
      margin-bottom: 20px;

      img {
        max-width: 432px;
        width: 100%;
        height: auto;
      }
    }

    &__socials {
      padding-left: 0;
      list-style: none;
      display: flex;
      margin-bottom: 27px;

      @include md {
        margin-bottom: 32px;
      }

      li {
        margin-right: 15px;

        @include md {
          margin-right: 12px;
        }
      }

      li a {
        text-decoration: none;
        border: 1px solid var(--green-light);
        border-radius: 50%;
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--green-light);
        transition: 0.2s all ease-in;

        @include md {
          height: 45px;
          width: 45px;
        }

        * {
          transition: 0.2s all ease-in;
        }

        &:hover,
        &:focus {
          color: var(--gold);
          border-color: var(--gold);

          svg,
          path,
          rect {
            fill: var(--gold);
          }
        }
      }
    }

    &__start-conversation {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / 40px
        var(--unnamed-font-family-brandon-grotesque);
      color: var(--white);
      letter-spacing: var(--unnamed-character-spacing-2);
      text-transform: uppercase;
      text-decoration: none;

      @include md {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1.6px;
        display: block;
      }

      &:after {
        content: '>';
        color: var(--green-light);
        padding-left: 12px;
      }
    }
  }

  &__nav {
    padding-top: 7px;
    &__title {
      color: var(--green-light);
      font-style: var(--unnamed-font-style-normal);
      font-weight: var(--unnamed-font-weight-300);
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 1.4px;
      color: var(--green-light);
      text-transform: var(--unnamed-text-transform-uppercase);
      margin-bottom: 13px;

      @include md {
        font-size: 11px;
        letter-spacing: 1.1px;
        margin-bottom: 18px;
      }
    }

    &__links {
      padding-left: 0;
      margin-bottom: 42px;
      list-style: none;

      @include md {
        margin-bottom: 47px;
      }

      li a {
        text-transform: uppercase;
        text-decoration: none;

        font-weight: 400;
        font-size: 14px;
        line-height: 26px;
        letter-spacing: 1.4px;
        color: var(--white);

        @include md {
          font-size: 11px;
          line-height: 20px;
          letter-spacing: 1.1px;
        }
      }
    }

    &__copyright {
      a {
        text-transform: uppercase;
        text-decoration: none;

        font-weight: 400;
        font-size: 14px;
        line-height: 26px;
        letter-spacing: 1.4px;
        color: var(--white);
        margin-right: 29px;

        &:nth-child(2) {
          margin-right: 8px;
        }

        &:last-of-type {
          white-space: nowrap;
        }

        @include md {
          font-size: 11px;
          line-height: 20px;
          letter-spacing: 1.1px;
          margin-right: 20px;
          margin-left: 0 !important;

          &:first-of-type {
            width: 100%;
            display: block;
          }
        }

        span {
          color: var(--green-light);
        }
      }
    }
  }
}
