@use './../abstracts/mixins' as *;

.menu {
  position: fixed;
  width: 100%;
  height: 100%;
  background: var(--dark-green);
  z-index: 99;
  display: none;

  &.visible {
    display: block;
  }

  &__header {
    padding-left: 43px;
    padding-top: 30px;
    display: flex;
    justify-content: space-between;

    @include md {
      padding-left: 23px;
      padding-top: 27px;
    }

    &__title {
      margin-top: 16px;

      @include md {
        margin-top: 0;
      }

      img {
        @include md {
          width: 205px;
        }
      }
    }

    &__hamburger {
      width: 45px;
      height: 45px;
      background: var(--gold);
      position: fixed;
      top: 30px;
      right: 48px;
      border: 0;
      border-radius: 50%;
      cursor: pointer;
      transition: 0.2s all ease-in;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      @include md {
        width: 34px;
        height: 34px;
        top: 16px;
        right: 8px;
      }
      svg {
        @include md {
          width: 18px;
          height: 18px;
        }
      }

      &:hover,
      &:focus {
        background-color: var(--green-light);
      }
    }
  }

  &__content {
    display: flex;
    padding-bottom: 30px;
    align-items: flex-start;
    padding-top: 150px;
    height: 100%;

    @include md {
      padding-top: 0;
      display: block;
      margin-top: 54px;
    }
  }

  &__login {
    padding-left: 104px;
    @include md {
      padding-left: 23px;
      padding-top: 21px;
    }

    &__title {
      color: var(--white);
      text-transform: uppercase;
      font-weight: 300;
      font-size: 32px;
      line-height: 60px;
      letter-spacing: 1.6px;
      margin-bottom: 22px;

      @include md {
        font-size: 20px;
        letter-spacing: 1px;
        margin-bottom: 10px;
      }
    }
  }

  &__nav {
    width: 45%;
    padding-left: 78px;
    padding-top: 25px;
    padding-bottom: 80px;
    border-right: 2px solid var(--gold);

    @include md {
      padding-top: 0;
      width: calc(100% - 46px);
      margin-left: 23px;
      padding-left: 0;
      padding-bottom: 15px;
      border-right: 0;
      border-bottom: 2px solid var(--gold);
    }

    ul {
      padding-left: 0;
      list-style: none;

      li a {
        text-decoration: none;
        color: var(--white);
        text-transform: uppercase;
        font-size: 42px;
        letter-spacing: 2.1px;
        line-height: 60px;
        font-weight: 300;
        margin-bottom: 4px;

        @include md {
          letter-spacing: 1.3px;
          font-size: 26px;
          line-height: 26px;
          display: block;
          margin-bottom: 14px;
        }

        &:hover,
        &:focus {
          color: var(--gold);
        }
      }
    }
  }
}
